.background {
	background-color: #f0f0f0;
	padding: 20px;
}

.mainBody {
	padding: 20px;
	min-height: 100vh;
	color: white;
	background-color: white;
	display: flex;
	flex-direction: column;
	font-family: 'Fira Sans', sans-serif;
}

.mainBody h1 {
	color: black;
	text-align: center;
	font-weight: bold;
}

.sectionBlue {
	display: flex;
	justify-content: space-evenly;
	height: fit-content;
	padding: 20px;
	align-items: center;
	text-align: left;
	background-color: #00b7ec;
	margin-bottom: 10px;
}

.mainBody hr {
	border: 5px solid #7fbb42;
	opacity: 100%;
}

.sectionBlue li {
	text-align: left;
}

.clickableImageContainer{
	width: fit-content;
	height: fit-content;
	overflow: hidden;
}

.clickableImageContainer .clickableImage {
	width: 626px;
	height: 626px;
	transition: transform 0.2s ease-in-out, filter 0.2s ease-in-out;
}

.clickableImageContainer .clickableImage:hover {
	transform: scale(1.2);
	filter: brightness(70%);
}

.img {
	float: left;
}
.cards {
display: flex;
justify-content: space-evenly;
}
